import styled from 'react-emotion'
import { injectGlobal } from 'emotion'

injectGlobal`
  * {
    font-size: 48px;
  }`

const Container = ({ className }) => (
   <div className={className}>T.E.S.T</div> 
)

const Index = styled(Container)`
    color: hotpink;
`

export default Index;


